import Application from "../lib/libSupport";
import { setDataBase, setUserConfig, setUserConfigDefault, setRoleOfUser, setUserID } from "../lib/libSupport";
import React from 'react';
import coreTx from "../tx/coreTx";
import libClient from "../lib/libClient";
import SelectApplication from "../screens/application";
import Login from "../screens/login";
class system extends coreTx {
	startLang = () => {
		var lang_join = {};
		if (this.input["sys_lang_data"] !== undefined)
			lang_join = this.input["sys_lang_data"];

		if (this.input["lang_data"] !== undefined) {
			for (var x in this.input.lang_data)
				lang_join[x] = this.input.lang_data[x];
		}
		else
			Application.AppException("#system.startLang", "lang_data is undefined", "data");

		Application.setAllLang(lang_join);
	}

	logout = () => {
		Application.clearSessionStorage();
	}

	loadDatabase = () => {
		if (this.input["database"] !== undefined)
			setDataBase(this.input.database);
		else
			Application.AppException("#system.loadDatabase", "database is undefined", "data");
	}
	loadParameterSystem = () => {
		if (this.input["paraServer"] !== undefined)
			Application.loadParameterSystem(this.input.paraServer);
		else
			Application.AppException("#system.loadParameterSystem", "paraServer is undefined", "data");
	}
	loadTxCodeFo = () => {
		//khng c xa
		//dng  c gi tr x l  client
		//c 1 tin trnh bo v fo gi  ti cc fo cha c  client
		//nn hm ny ch c gi tr gip fo ng chun
	}

	//hm ny dnh ring cho socket nn c run tip tx fo
	readWebsocket = () => {
		if (this.input["run_fo"] !== undefined)
			for (var i = 0; i < this.input.run_fo.length; i++)
				if (this.input.run_fo[i]["fo"] !== undefined)
					libClient.fo(this.input.run_fo[i].fo, {});
	}
	reloadPage = () => {
		window.location.reload();
	}
	//17 10 2019
	//hm t ng vn hnh, hin th thng tin li t server n ngi dng
	showError = () => {

	}
	//04 07 2019 to project login ring chuyn lm vic login
	//nu vo trang ny mc nh tt c session iu b xa ht
	callFormLogin = () => {
		//xa session  client trc v c th session ny ht hn
		Application.clearSessionStorageAll();

		//gi form login bnh thng
		var lang_login = {};
		var lang_ = Application.getAllLang()[Application.getLangKey()];
		for (var xaa in lang_)
			if (xaa.includes("login_"))
				lang_login[xaa] = lang_[xaa];

		Application.newForm(<Login lang={lang_login} key="1" />);
		document.title = Application.getLang("title");
	}
	addUserProfileDefaultSys = () => {
		let UserProfileDefaultSys__ = this.input["UserProfileDefaultSys"];
		setUserConfigDefault(UserProfileDefaultSys__);

		//set ngn ng mc nh
		//tm ngn ng mc nh
		let list_lang_config = UserProfileDefaultSys__.list_lang_config;
		for (var i = 0; i < list_lang_config.length; i++)
			if (list_lang_config[i].selected) {
				Application.setLangKey(list_lang_config[i].key);
				break;
			}
	}
	downAllApplication = () => {
		if (this.input.list_appliaction !== undefined)
			Application.setData("list_appliaction", this.input.list_appliaction)
	}
	statusLogin = () => {
		//chc nng ny cp nhp li thng tin ca trnh duyt  c  server
		//tng tnh bo mt t trnh duyt gi danh
		//if (this.input['read_info_client'] !== undefined)
		//	if (this.input['read_info_client'].ip !== undefined) {
		//		Application.setMyDeviceIP(this.input['read_info_client'].ip);
		//	}



		if (this.input["login-status"] !== undefined) {
			if (this.input["login-status"]["status_login"] !== undefined)
				if (this.input["login-status"]["status_login"] === "login#127") {
					var has_full_data_login = true;
					if (this.input["loadUserConfigDefault"] !== undefined)
						setUserConfigDefault(this.input["loadUserConfigDefault"]);
					else {
						has_full_data_login = false;
						Application.AppException("#system.statusLogin", "loadUserConfigDefault is undefined", "data");
					}
					//thng tin user cu hnh
					if (this.input["loadUserConfig"] !== undefined)
						setUserConfig(this.input["loadUserConfig"]);
					else {
						if (has_full_data_login)
							has_full_data_login = false;
						Application.AppException("#system.statusLogin", "loadUserConfig is undefined", "data");
					}
					//user_role
					if (this.input["loadUserRole"] !== undefined)
						setRoleOfUser(this.input["loadUserRole"]);
					else {
						if (has_full_data_login)
							has_full_data_login = false;
						Application.AppException("#system.statusLogin", "loadUserRole is undefined", "data");
					}

					if (this.input["loadUserID"] !== undefined) {
						if (this.input["loadUserID"]["id"] !== undefined)
							setUserID(this.input["loadUserID"]["id"]);
						else {
							if (has_full_data_login)
								has_full_data_login = false;
							Application.AppException("#system.statusLogin", "loadUserID[id] is undefined", "data");
						}
					}
					else {
						if (has_full_data_login)
							has_full_data_login = false;
						Application.AppException("#system.statusLogin", "loadUserID is undefined", "data");
					}
					//ti thm menu
					//ti thm b cc mn hnh lm vic

					//t xa mn hnh login
					if (has_full_data_login) {
						//m mn hnh chn app
						Application.newForm(<SelectApplication key="2" />);
						return true;//ok
					}
					else
						Application.AppException("#system.statusLogin", "Login false", "system");
				}
			if (this.input["login-status"]["error_login"] !== undefined) {
				/*
				if (Application.checkIsMobi()) {
					let mobiCore = libFrameworkMobi.getCore();
					mobiCore.toast.create({
						text: Application.getLang(this.input["login-status"]["error_login"]),
						position: 'center',
						closeTimeout: 2000,
					}).open();
				}
				else
					alert(Application.getLang(this.input["login-status"]["error_login"]));
					*/
			}

		}
		else
			Application.AppException("#system.login-status", "login-status is undefined", "system");
	}
	container = () => {

	}
	//hm x l a data vo kho chung  client
	data = () => {
		for (var x in this.input)
			Application.setData(x, this.input[x]);
	}
}
export default system;