//phn tch v tr v trnh t x l tip theo
//code : m v tr x l hin ti
//flow_layout: mi quan h, 
//nu nhiu hn 2 quan h  cng 1 kt qu l do iu kin b yu, 
//lun lun tr v 1 thng tin x l

//all_id_button: danh sch cc x l
export const readFlowFunctionClientStep = (code, flow_layout, all_id_button) => {
	for (var i_fl = 0; i_fl < flow_layout.length; i_fl++) {
		if (flow_layout[i_fl]["sourceId"] != null) {
			if (flow_layout[i_fl].sourceId === code) {
				//tm c v tr tip theo
				if (flow_layout[i_fl]["targetId"] != null) {
					//tra cu
					for (var i_id = 0; i_id < all_id_button.length; i_id++) {
						if (all_id_button[i_id]["action_code"] != null) {
							if (all_id_button[i_id].action_code === flow_layout[i_fl].targetId)
								return all_id_button[i_id];//tm thy
						}
					}
				}
			}
		}
	}
	//khng thy
	return undefined;
}
