	//title c khai bo trong mapping (nh x) ca designDatabase
	static getLangWithData(keyLang, data) {
		var lang_ = getLang(keyLang);
		if (data !== undefined) {
			if (data["table"] !== undefined && data["data"] !== undefined) {
				//data ca d liu {}
				var data_ob = data.data;
				//ly cu trc table ra
				var table_ = getDataBaseTable(data.table);
				if (table_ !== undefined)
					if (table_["detail"] != null) {
						var table_detail = table_["detail"];
						for (var i_col = 0; i_col < table_detail.length; i_col++) {
							var table_detail_col = table_detail[i_col];
							if (table_detail_col["code"] != null && table_detail_col["mapping"] != null) {
								if (data_ob[table_detail_col.code] !== undefined) {
									lang_ = lang_.replace("[" + table_detail_col.code + "]", data_ob[table_detail_col.code]);
									lang_ = lang_.replace("[" + table_detail_col.mapping + "]", data_ob[table_detail_col.code]);
								}
								else {
									lang_ = lang_.replace("[" + table_detail_col.code + "]", "");
									lang_ = lang_.replace("[" + table_detail_col.mapping + "]", "");
								}
							}
						}
					}
			}
		}
		return lang_;
	}
