	static convertFoOfApp_fo_check(fo_check) {
		//do fo_check l thnh phn c key fo lm key chnh nn cn chuyn key na
		//to 1 i tng mi
		var fo_check_new = {};

		//chuyn data
		for (var x in fo_check)
			if (fo_check[x].includes("#")
				&& fo_check[x].includes(":")) {
				var tx_code_new = x.substring(x.indexOf(":") + 1, x.length);
				fo_check_new[tx_code_new] = tx_code_new;
			}
			else
				fo_check_new[x] = x;//gi ca app hin ti
		return fo_check_new;
	}
	//dnh cho fo_arr
	static convertFoOfApp_fo_arr(fo_arr) {
		var for_arr_new = [];
		for (var i = 0; i < fo_arr.length; i++)
			if (fo_arr[i]["txcode"] !== undefined) {
				for_arr_new[i] = JSON.parse(JSON.stringify(fo_arr[i]));
				if (fo_arr[i].txcode.includes("#")
					&& fo_arr[i].txcode.includes(":")) {
					var tx_ = fo_arr[i].txcode;
					for_arr_new[i].txcode = tx_.substring(tx_.indexOf(":") + 1, tx_.length);
				}
			}
		return for_arr_new;
	}

