	//chnh sa data
	static updateData(table, ob_data, table_pk) {
		if (table_pk === undefined)
			table_pk = "AFGHkeyIndexWJ";

		if (cache.data[table] === undefined) {
			cache.data[table] = [];
			return;//khng c  sa
		}
		if (ob_data[table] !== undefined)
			for (var i = 0; i < cache.data[table].length; i++) {
				if (cache.data[table][i][table][table_pk] !== undefined
					&& ob_data[table][table_pk] !== undefined) {
					if (cache.data[table][i][table].AFGHkeyIndexWJ === ob_data[table].AFGHkeyIndexWJ) {
						cache.data[table][i] = ob_data;
						return; // xa xong
					}
				}
			}
	}


	static deleteData(table, ob_data, pk_col) {
		if (pk_col === undefined)
			pk_col = "AFGHkeyIndexWJ";

		if (cache.data[table] === undefined) {
			cache.data[table] = [];
			return; // khng c  xa
		}

		if (ob_data[table] !== undefined)
			for (var i = 0; i < cache.data[table].length; i++) {
				if (cache.data[table][i][table][pk_col] !== undefined
					&& ob_data[table][pk_col] !== undefined) {
					if (cache.data[table][i][table][pk_col] === ob_data[table][pk_col]) {
						cache.data[table].splice(i, 1);
						return; // xa xong
					}
				}
			}
	}
