	//support search v sort
	//Hm gip cho cc vic tm kim  li nhanh
	static searchTable(infoSearch, dataSearch) {
		var dataSearch_copy = [];
		if (infoSearch !== undefined && dataSearch !== undefined) {
			//nu tt c search bng trng th khi phc li
			var check_has_search = false;
			for (var i_search_check = 0; i_search_check < infoSearch.length; i_search_check++)
				//tm theo cc ct
				if (infoSearch[i_search_check]["search"] !== undefined)
					if (infoSearch[i_search_check]["search"].trim() !== "") {
						check_has_search = true;
						break;
					}

			if (check_has_search) {
				for (var i = 0; i < dataSearch.length; i++)
					if (dataSearch[i] !== undefined)
						for (var i_search = 0; i_search < infoSearch.length; i_search++)
							//tm theo cc ct
							if (infoSearch[i_search]["search"] !== undefined
								&& infoSearch[i_search]["code"] !== undefined) {
								var code_col = infoSearch[i_search]["code"];
								if (dataSearch[i][code_col] !== undefined
									&& infoSearch[i_search].search.trim() !== "")
									if (dataSearch[i][code_col].includes(infoSearch[i_search].search.trim())) {
										dataSearch_copy[dataSearch_copy.length] = dataSearch[i];
										break;
									}
							}
			}
			else
				dataSearch_copy = dataSearch;//nh c
		}
		return dataSearch_copy;
	}
