	//ly random 1 gii thut bt k, nu khng c th s l 1
	static rsa_getAlgorithm() {
		var random_ = Math.floor(Math.random() * 10);//0->9
		if (cache.rsa.algorithm[random_] !== undefined)
			return cache.rsa.algorithm[random_];
		return cache.rsa.algorithm[1];
	}
	//ly thut gii bin dch gi tin tr v
	//thut gii ny s chy ngc vi trn server
	static rsa_response_getAlgorithm() {
		var random_ = Math.floor(Math.random() * 10);//0->9
		if (cache.rsa.algorithm_response[random_] !== undefined)
			return cache.rsa.algorithm_response[random_];
		return cache.rsa.algorithm_response["1"];
	}
	static rsa_setAlgorithm(algorithm) {
		cache.rsa.algorithm = Object.assign(cache.rsa.algorithm, algorithm);
	}
	static rsa_setPublicKey(rsa_publicKey) {
		cache.rsa.publicKey = rsa_publicKey;
	}
	static rsa_getPublicKey() {
		return cache.rsa.publicKey;
	}
