import Application from "./lib/libSupport";
import React from 'react';
import ReactDOM from 'react-dom';
import JWebUITemplateControl from "./template/jWebUITemplateControl";
import ApplicationLoading from "./screens/applicationLoading";
import { start_percent } from "./lib/libManagerLoading";

ReactDOM.render(<ApplicationLoading ></ApplicationLoading>, document.getElementById('loading'));

let theme = null;
let configTemplate;
export function callLoadFinal() {
    window.process_loading.loading.main_script--;
    window.processLoadingGUI("Main Script", (window.process_loading.max.main_script - window.process_loading.loading.main_script) + "/" + window.process_loading.max.main_script);

    if (window.process_loading.loading.template_script === 0
        && window.process_loading.loading.main_script === 0)
        window.callStart();
}
export function callLoadTemplateCss(template_css_name) {
    window.process_loading.loading.template_css--;
    window.processLoadingGUI("Template CSS", (window.process_loading.max.template_css - window.process_loading.loading.template_css) + "/" + window.process_loading.max.template_css, template_css_name);

    //c gi 2 ln begin lin tc l do  template logic thi
    //ch  FW CSS khng, nn 2 hm ny c  vy nha
    //managerTemplate_begin v managerTemplate_begin_2
    //ti cc thnh phn cho template
    if (window.process_loading.loading.template_css === 0)
        window.obManagerTemplate_libManagerTemplate.managerTemplate_begin_2(configTemplate.template, configTemplate.screen_working, theme, true, callLoadComponent);
}

export function callLoadComponent() {
    window.process_loading.loading.template_component--;
    window.processLoadingGUI("Template Component", (window.process_loading.max.template_component - window.process_loading.loading.template_component) + "/" + window.process_loading.max.template_component);

    //bt u cc phn trn app 
    Application.start(window.mainstart.configGlobal);

    //to mn hnh loading
    ReactDOM.render(<JWebUITemplateControl
        template_select={configTemplate.template}
        screen_working={configTemplate.screen_working}
        theme={theme}
    ></JWebUITemplateControl>, document.getElementById('root'));
}
export function callStart() {
    if (window.mainstart.rsa !== undefined && window.mainstart.configGlobal) {
        Application.rsa_setPublicKey(window.mainstart.rsa);

        Application.template_setConfigTemplate(window.mainstart.template_login);
        //m thng tin t url gii m t tham s s
        // login  cho  tham s ny, m khng cn ti thm  trong app
        //sau khi chy qua hm Application.start th tham s  s c khi to

        configTemplate = Application.template_getConfigTemplate();
        //ly thng tin template ca app
        //ti template v t y
        //gm template v loading
        //bt u ti css giao din
        if (configTemplate.theme !== undefined)
            theme = configTemplate.theme;

        window.obManagerTemplate_libManagerTemplate.managerTemplate_begin(configTemplate.template, configTemplate.screen_working, theme, true, callLoadTemplateCss, window.mainstart.template_host_dev);
    }
}
export function processLoadingGUI(str_type_data, system_count, name_info) {
    start_percent(str_type_data, system_count, name_info);
}
window.processLoadingGUI = processLoadingGUI;
window.callStart = callStart;

//do lc pht trin(dev) h thng khng bit l truyc cp vo u  ly thng tin
//nn fix tn mc nh nh sau: 
var url_get_config = "//jwebui.com:8080";
if (Application.checkIsPublic())
    url_get_config = "";

Application.addSript(url_get_config + "/webchannel/template_dynamic", callLoadFinal);
Application.addSript(url_get_config + "/webchannel/config-client", callLoadFinal);