// eslint-disable-next-line
function rsa_Decrypt(a) {
    var crypt = new window.JSEncrypt({ default_key_size: 1024 });
    crypt.setPrivateKey(web_system.private_);

    var int_slipt = 172;
    var rsa_slip = "";
    var a_cut = a;
    while (a_cut.length > 0) {
        var a__ = a_cut.substring(0, int_slipt);
        a_cut = a_cut.substring(int_slipt);
        rsa_slip += crypt.decrypt(a__);
    }
    return rsa_slip;
}
export const rsa_Encrypted = (a) => {
    var encrypt = new window.JSEncrypt({ default_key_size: 1024 });
    encrypt.setPublicKey(web_system.public_);
    var int_slipt = 117;
    var rsa_slip = "";
    var a_cut = a;
    while (a_cut.length > 0) {
        var a__ = "";

        //tnh tng lng chi, 
        //nu 1byte th l 1
        //nu 2byte th l 2
        var utf_length = 0;
        var a_index = 0;
        while (a_cut.length > a_index) {
            //khng c kim tra chui vt chiu di khng 
            //v lng k t cn li c th nhiu m utf8 lm chiu di tuy nh nh hn 116
            var c = a_cut.charCodeAt(a_index);

            if (c < 128)
                utf_length++;
            else if ((c > 127) && (c < 2048))
                utf_length = utf_length + 2;
            else
                utf_length = utf_length + 3;

            if (utf_length >= int_slipt)
                break;
            a__ += a_cut[a_index];
            a_index++;
        }

        a_cut = a_cut.substring(a_index, a_cut.length);
        rsa_slip += encrypt.encrypt(a__);
    }
    return rsa_slip;
}