class coreTx {

    constructor(context, input, input_action, fo_step, fo_arr_step, input_fo, list_bo_new, fo_arr) {
        if (context === undefined)
            this.context = {};
        else
            this.context = context;

        if (input === undefined)
            this.input = {};
        else
            this.input = input;

        if (input_action === undefined)
            this.input_action = {};
        else
            this.input_action = input_action;

        //14 06 2019 cp nhp chc nng modal confirm (ch xc nhn)
        //cc bin c truyn vo  b c hng chy ca fo
        //cc thng s ny khng bao gi trng
        //fo_step: v tr ang chy hin ti
        //input_fo: d liu  thu thp ca input fo t u n bc chy hin ti
        //list_bo_new: d liu  thu thp c ca fo cho ra cc bo
        this.fo_step = fo_step;
        this.fo_arr_step = fo_arr_step;
        this.input_fo = input_fo;
        this.list_bo_new = list_bo_new;
        this.fo_arr = fo_arr;
    }

    getInputAction = () => {
        return this.input_action;
    }
    getInput = () => {
        return this.input;
    }
    //thm d liu vo kho chung
    addPackFo(key, data) {
        if (data !== undefined)
            this.input_action[key] = data;
    }

    //to gi roll back  cc h thng khc gi v ng nht
    buildPackRollBack() {
        //b rollback
        return {
            fo_step: this.fo_step,
            fo_arr_step: this.fo_arr_step,
            context: this.context,
            input_fo: this.input_fo,
            input_action: this.input_action,
            list_bo_new: this.list_bo_new,
            fo_arr: this.fo_arr
        };
    }
}
export default coreTx;