import Application, { getKey_D, getWebSV, getLangKey, getKey_APP, getUserID } from "./libSupport";
class wizA {
	constructor() {
		//03 06 g bo cho txCode
		this.bo = [];

		//t tm v thit lp cc thng s
		const now = new Date();
		this.info = {
			d: getKey_D(),
			u: getWebSV(),
			lang: getLangKey(),
			app: getKey_APP(),
			uid: getUserID(),
			my_device: Application.getMyDevice(),
			request_time: now.getTime()
		}
		//bt chc nng debug c ngha cc li s c ghi li vo 
		//bo_trace v fo_trace
		//xem cng lc vi show trace (s c thm 2 thng tin trn)
		if (Application.isDebug())
			this.info["dgu"] = Application.isDebug();
	}

	setBo(bo, key_feedback) {
		//M t Feedback
		//Chc nng gm 3 phn:
		//1. T nh du gi tin: khi 1 fo/bo c gi s b nh du 1 m v m ny s xuyn sut c gi tin 
		//vd: click 1 button c nh 1 m l asdd-35345-fdfa-234 (gi l m Feedback) th khi gi thng tin ln API, SQL v tr v iu c lu li vo th mc m Feedback.
		//2. Khi thng bo li validate t component s xut hin 1 button (c t n), click vo s scroll n v tr component  (nu component  trong tab, mutivalue) th s t m hoc click m tab .
		//3. Thng bo t logic application, c 1 form [quy nh mi lin kt gia t kha trn thng bo] v component code v tn 1 tx_fo x l (hm trong tx)
		//Hm tx_fo c th chuyn i t thng bo thnh t kha, t t kha jwebui s tra vo [quy nh mi lin kt gia t kha trn thng bo]  n component nh  mc 2. Nu khng c component code th button s khng hin ra.

		//Chc nng feedback khi ngi dng chn s t thu thp c 3 thng tin trn + d liu form ng ang m => gi vo ti khon mail ca user h tr chnh h thng jwebui. (s nghin cu thm chc nng chp hnh)
		if (key_feedback !== undefined)
			this.info["key_feedback"] = key_feedback;
		this.bo = bo;
	}

	//ch ly gi tin theo cu trc bo (txCode)
	getForTxCode() {
		//30 08 2019
		//m chc nng checksum cho cc gi tin
		//gii thut checksum ca jwebui
		//mc tiu checksum c gi tin, loi tr m checksum
		//bc 1: to gi tin hon chnh
		var p = {
			info: this.info,
			bo: this.bo
		};

		var str_p = JSON.stringify(p);

		//bc 2 gn m checksum vo gi tin li
		p.info["checksum"] = this.checkSum(this.stringToArray(str_p));
		return p;
	}

	checkSum(a) {
		var len = a.length,
			fnv = 0;
		for (var i = 0; i < len; i++)
			fnv = (fnv + (((fnv << 1) + (fnv << 4) + (fnv << 7) + (fnv << 8) + (fnv << 24)) >>> 0)) ^ (a[i] & 0xff);
		return fnv >>> 0;
	};
	stringToArray(str) {
		var utf8 = [];
		for (var i = 0; i < str.length; i++) {
			var charcode = str.charCodeAt(i);
			if (charcode < 0x80) utf8.push(charcode);
			else if (charcode < 0x800) {
				utf8.push(0xc0 | (charcode >> 6),
					0x80 | (charcode & 0x3f));
			}
			else if (charcode < 0xd800 || charcode >= 0xe000) {
				utf8.push(0xe0 | (charcode >> 12),
					0x80 | ((charcode >> 6) & 0x3f),
					0x80 | (charcode & 0x3f));
			} else { // surrogate pair
				i++;
                /**
                 * UTF-16 encodes 0x10000-0x10FFFF by
                 * subtracting 0x10000 and splitting the
                 * 20 bits of 0x0-0xFFFFF into two halves
                 */
				charcode = 0x10000 + (((charcode & 0x3ff) << 10)
					| (str.charCodeAt(i) & 0x3ff))
				utf8.push(0xf0 | (charcode >> 18),
					0x80 | ((charcode >> 12) & 0x3f),
					0x80 | ((charcode >> 6) & 0x3f),
					0x80 | (charcode & 0x3f));
			}
		}

		if (this.hasTyped) {
			return new Uint8Array(utf8);
		}

		return utf8;
	};
	hasTyped() {
		if (!('Uint8Array' in window)) {
			return false;
		}

		try {
			var a = new window.Uint8Array(10);
			a[0] = 100;
			if (a[0] === 100) {
				return true;
			}
			return false;
		} catch (e) {
			return false;
		}
	};
}
export default wizA;